import React from "react";
import { Card, Statistic } from "antd";
import { DeploymentUnitOutlined } from "@ant-design/icons/es/icons";

export default function TotalEndpoints({ totalEndpoints }) {
  return (
    <Card bordered={false}>
      <Statistic
        title="Total Endpoints"
        value={totalEndpoints}
        valueStyle={{ color: "#1890ff" }}
        suffix="Endpoints"
        prefix={<DeploymentUnitOutlined />}
      />
    </Card>
  );
}
